@font-face {
  font-family: "SF Pro Display";
  src: url("../fonts/SFPro/SFProDisplay-Medium.eot");
  src: local("SF Pro Display Medium"), local("SFProDisplay-Medium"), url("../fonts/SFPro/SFProDisplay-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/SFPro/SFProDisplay-Medium.woff2") format("woff2"), url("../fonts/SFPro/SFProDisplay-Medium.woff") format("woff"), url("../fonts/SFPro/SFProDisplay-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "SF Pro Display";
  src: url("../fonts/SFPro/SFProDisplay-Semibold.eot");
  src: local("SF Pro Display Semibold"), local("SFProDisplay-Semibold"), url("../fonts/SFPro/SFProDisplay-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/SFPro/SFProDisplay-Semibold.woff2") format("woff2"), url("../fonts/SFPro/SFProDisplay-Semibold.woff") format("woff"), url("../fonts/SFPro/SFProDisplay-Semibold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "SF Pro Display";
  src: url("../fonts/SFPro/SFProDisplay-Regular.eot");
  src: local("SF Pro Display Regular"), local("SFProDisplay-Regular"), url("../fonts/SFPro/SFProDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/SFPro/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFPro/SFProDisplay-Regular.woff") format("woff"), url("../fonts/SFPro/SFProDisplay-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.step {
  margin: 0 16px 6px;
}

.cards {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.step-two {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 32px;
}

.header-first {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 16px 8px;
  background-color: rgb(255, 255, 255);
}
.header-first__title {
  margin: 0 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
}
.header-first__text {
  margin: 0 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: rgb(92, 92, 92);
}

.header-second {
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  min-height: 72px;
  padding: 12px 8px;
  background-color: rgb(255, 255, 255);
}
.header-second__title {
  margin: 0 8px;
  font-weight: 600;
}
.header-second__back-button {
  display: none;
  width: 48px;
  height: 48px;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0 8px 0 0;
  border: none;
  background-color: transparent;
  color: rgb(92, 92, 92);
  cursor: pointer;
}
.header-second__back-icon {
  width: 32px;
  height: 32px;
}
.header-second__steps {
  display: none;
  flex-shrink: 0;
  flex-wrap: nowrap;
  gap: 4px;
  margin-left: auto;
  margin-right: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: rgb(92, 92, 92);
}
.header-second.selected .header-second__back-button {
  display: flex;
}
.header-second.selected .header-second__steps {
  display: flex;
}

.card {
  display: flex;
  flex-direction: column;
  padding: 16px 24px;
  border-radius: 24px;
  border: 1px solid rgba(0, 0, 0, 0.04);
  transition: all 0.2s;
}
.card.interactive {
  cursor: pointer;
}
.card:nth-child(7n+1) {
  background-color: rgb(244, 240, 225);
}
.card:nth-child(7n+2) {
  background-color: rgb(230, 241, 228);
}
.card:nth-child(7n+3) {
  background-color: rgb(231, 235, 247);
}
.card:nth-child(7n+4) {
  background-color: rgb(241, 228, 233);
}
.card:nth-child(7n+5) {
  background-color: rgb(241, 235, 228);
}
.card:nth-child(7n+6) {
  background-color: rgb(228, 240, 241);
}
.card:nth-child(7n) {
  background-color: rgb(238, 228, 241);
}
.card.interactive:hover:nth-child(7n+1) {
  background-color: rgb(233, 225, 195);
}
.card.interactive:hover:nth-child(7n+2) {
  background-color: rgb(205, 227, 201);
}
.card.interactive:hover:nth-child(7n+3) {
  background-color: rgb(194, 204, 235);
}
.card.interactive:hover:nth-child(7n+4) {
  background-color: rgb(227, 201, 211);
}
.card.interactive:hover:nth-child(7n+5) {
  background-color: rgb(227, 215, 201);
}
.card.interactive:hover:nth-child(7n+6) {
  background-color: rgb(201, 225, 227);
}
.card.interactive:hover:nth-child(7n) {
  background-color: rgb(221, 201, 227);
}
.card__name {
  display: flex;
  align-items: flex-start;
  gap: 4px;
}
.card__name-hit {
  display: none;
}
.card.interactive .card__name-hit {
  display: flex;
  align-items: center;
}
.card__name-hit-icon {
  width: 24px;
  height: 24px;
  color: rgb(255, 59, 56);
}
.card__name-hit-text {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: rgb(219, 40, 53);
}
.card__status {
  display: none;
  margin-top: 2px;
  font-size: 12px;
  line-height: 16px;
  color: rgb(92, 92, 92);
}
.card.interactive .card__status {
  display: block;
}
.card.interactive .card__address {
  display: none;
}
.card.interactive .card__description {
  display: none;
}
.card__address {
  margin-top: 2px;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 16px;
  color: rgb(92, 92, 92);
}
.card__description {
  margin-top: 8px;
  font-size: 14px;
  line-height: 20px;
}
.card__price {
  display: flex;
  flex-wrap: nowrap;
  gap: 2px;
  margin-top: 8px;
  font-weight: 600;
}

.tabs {
  display: flex;
  flex-wrap: nowrap;
  border-radius: 8px;
  box-shadow: inset 0 0 0 1px #d6d6d6;
  margin-left: auto;
}
.tabs__button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 36px;
  margin: 0;
  padding: 0 16px;
  border-radius: 8px;
  border: 1px solid transparent;
  background-color: transparent;
  color: #5c5c5c;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
  transition: all 0.2s;
}
.tabs__button:hover {
  color: #0190db;
}
.tabs__button.active {
  border-color: rgba(1, 144, 219, 0.32);
  background-color: #ebf6fc;
  color: #006fbc;
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  min-height: 100%;
  font-family: "SF Pro Display", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: rgb(31, 31, 31);
  background-color: rgb(255, 255, 255);
}

.main {
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}